import React, { useState, useEffect } from "react";
import {
  Container,
  Row,
  Col,
  Card,
  Button,
  Form,
  Table,
  Pagination,
  Modal,
  Alert,
  Spinner,
} from "react-bootstrap";
import { useNavigate } from "react-router-dom";
import api from "../utils/axiosConfig";
import "../styles/AdminPage.css";

const AdminPage = () => {
  const navigate = useNavigate();
  const [isAdmin, setIsAdmin] = useState(false);
  const [isLoading, setIsLoading] = useState(true);
  const [words, setWords] = useState([]);
  const [totalPages, setTotalPages] = useState(1);
  const [currentPage, setCurrentPage] = useState(1);
  const [searchTerm, setSearchTerm] = useState("");
  const [selectedLevel, setSelectedLevel] = useState("");
  const [showUploadModal, setShowUploadModal] = useState(false);
  const [showEditModal, setShowEditModal] = useState(false);
  const [editWord, setEditWord] = useState({
    id: null,
    english: "",
    korean: "",
    level: 1,
  });
  const [file, setFile] = useState(null);
  const [uploadResult, setUploadResult] = useState(null);
  const [error, setError] = useState("");

  //   
  useEffect(() => {
    const checkAdmin = async () => {
      try {
        const response = await api.get("/check_admin");
        setIsAdmin(response.data.is_admin);
        if (!response.data.is_admin) {
          navigate("/");
        } else {
          fetchWords(1);
        }
      } catch (error) {
        console.error("   :", error);
        navigate("/");
      } finally {
        setIsLoading(false);
      }
    };

    checkAdmin();
  }, [navigate]);

  //   
  const fetchWords = async (page) => {
    setIsLoading(true);
    try {
      let url = `/admin/words?page=${page}`;
      if (searchTerm) url += `&search=${encodeURIComponent(searchTerm)}`;
      if (selectedLevel) url += `&level=${selectedLevel}`;

      const response = await api.get(url);
      setWords(response.data.words);
      setTotalPages(response.data.pages);
      setCurrentPage(response.data.current_page);
    } catch (error) {
      console.error("   :", error);
      setError("     .");
    } finally {
      setIsLoading(false);
    }
  };

  //  
  const handleSearch = (e) => {
    e.preventDefault();
    fetchWords(1);
  };

  //  
  const handlePageChange = (page) => {
    fetchWords(page);
  };

  //  
  const handleDeleteWord = async (wordId) => {
    if (!window.confirm("   ?")) return;

    try {
      await api.delete(`/admin/words/${wordId}`);
      fetchWords(currentPage);
    } catch (error) {
      console.error("  :", error);
      setError("    .");
    }
  };

  //    
  const handleEditModalOpen = (word) => {
    setEditWord({
      id: word.id,
      english: word.english,
      korean: word.korean,
      level: word.level,
    });
    setShowEditModal(true);
  };

  //   
  const handleSaveEdit = async () => {
    try {
      await api.put(`/admin/words/${editWord.id}`, editWord);
      setShowEditModal(false);
      fetchWords(currentPage);
    } catch (error) {
      console.error("  :", error);
      setError("    .");
    }
  };

  //   
  const handleFileUpload = async (e) => {
    e.preventDefault();
    if (!file) {
      setError(" .");
      return;
    }

    const formData = new FormData();
    formData.append("file", file);

    try {
      const response = await api.post("/admin/upload_words", formData, {
        headers: {
          "Content-Type": "multipart/form-data",
        },
      });
      setUploadResult(response.data);
      fetchWords(1);
    } catch (error) {
      console.error("  :", error);
      setError(
        error.response?.data?.error || "    ."
      );
    }
  };

  //  
  const handleCreateWordSet = async () => {
    try {
      const response = await api.post("/admin/create_word_set");
      alert(`  . ID: ${response.data.id}`);
    } catch (error) {
      console.error("  :", error);
      setError("    .");
    }
  };

  //  
  const renderPagination = () => {
    const items = [];
    for (let i = 1; i <= totalPages; i++) {
      items.push(
        <Pagination.Item
          key={i}
          active={i === currentPage}
          onClick={() => handlePageChange(i)}
        >
          {i}
        </Pagination.Item>
      );
    }

    return (
      <Pagination className="justify-content-center mt-4">
        <Pagination.Prev
          onClick={() => handlePageChange(Math.max(1, currentPage - 1))}
          disabled={currentPage === 1}
        />
        {items}
        <Pagination.Next
          onClick={() =>
            handlePageChange(Math.min(totalPages, currentPage + 1))
          }
          disabled={currentPage === totalPages}
        />
      </Pagination>
    );
  };

  if (isLoading && !isAdmin) {
    return (
      <Container
        className="d-flex justify-content-center align-items-center"
        style={{ minHeight: "80vh" }}
      >
        <Spinner animation="border" />
      </Container>
    );
  }

  return (
    <Container className="admin-page py-4">
      <h1 className="mb-4"> </h1>

      {error && <Alert variant="danger">{error}</Alert>}

      <Card className="mb-4">
        <Card.Header as="h5"> </Card.Header>
        <Card.Body>
          <Row className="mb-3">
            <Col md={6}>
              <Form onSubmit={handleSearch}>
                <Row>
                  <Col md={5}>
                    <Form.Control
                      type="text"
                      placeholder=" ..."
                      value={searchTerm}
                      onChange={(e) => setSearchTerm(e.target.value)}
                    />
                  </Col>
                  <Col md={3}>
                    <Form.Select
                      value={selectedLevel}
                      onChange={(e) => setSelectedLevel(e.target.value)}
                    >
                      <option value=""> </option>
                      <option value="1"> 1</option>
                      <option value="2"> 2</option>
                      <option value="3"> 3</option>
                      <option value="4"> 4</option>
                      <option value="5"> 5</option>
                    </Form.Select>
                  </Col>
                  <Col md={4}>
                    <Button type="submit" variant="primary">
                      
                    </Button>
                  </Col>
                </Row>
              </Form>
            </Col>
            <Col md={6} className="text-end">
              <Button
                variant="success"
                className="me-2"
                onClick={() => setShowUploadModal(true)}
              >
                CSV 
              </Button>
              <Button variant="primary" onClick={handleCreateWordSet}>
                 
              </Button>
            </Col>
          </Row>

          {isLoading ? (
            <div className="text-center py-4">
              <Spinner animation="border" />
            </div>
          ) : (
            <>
              <Table striped bordered hover responsive>
                <thead>
                  <tr>
                    <th>ID</th>
                    <th></th>
                    <th></th>
                    <th></th>
                    <th></th>
                    <th> </th>
                    <th></th>
                  </tr>
                </thead>
                <tbody>
                  {words.length > 0 ? (
                    words.map((word) => (
                      <tr key={word.id}>
                        <td>{word.id}</td>
                        <td>{word.english}</td>
                        <td>{word.korean}</td>
                        <td>{word.level}</td>
                        <td>{word.used ? "" : ""}</td>
                        <td>
                          {word.last_modified
                            ? new Date(word.last_modified).toLocaleString()
                            : "-"}
                        </td>
                        <td>
                          <Button
                            variant="outline-primary"
                            size="sm"
                            className="me-1"
                            onClick={() => handleEditModalOpen(word)}
                          >
                            
                          </Button>
                          <Button
                            variant="outline-danger"
                            size="sm"
                            onClick={() => handleDeleteWord(word.id)}
                          >
                            
                          </Button>
                        </td>
                      </tr>
                    ))
                  ) : (
                    <tr>
                      <td colSpan="7" className="text-center">
                         .
                      </td>
                    </tr>
                  )}
                </tbody>
              </Table>

              {renderPagination()}
            </>
          )}
        </Card.Body>
      </Card>

      {/*    */}
      <Modal show={showUploadModal} onHide={() => setShowUploadModal(false)}>
        <Modal.Header closeButton>
          <Modal.Title>CSV  </Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <p>
            CSV  : <code>english,korean,level</code>
          </p>
          <p>    .</p>

          <Form onSubmit={handleFileUpload}>
            <Form.Group className="mb-3">
              <Form.Label>CSV  </Form.Label>
              <Form.Control
                type="file"
                accept=".csv"
                onChange={(e) => setFile(e.target.files[0])}
              />
            </Form.Group>

            {uploadResult && (
              <Alert variant="success">
                <p> !</p>
                <ul>
                  <li> : {uploadResult.added}</li>
                  <li> : {uploadResult.updated}</li>
                  <li>: {uploadResult.errors}</li>
                </ul>
                {uploadResult.error_details.length > 0 && (
                  <>
                    <p> :</p>
                    <ul>
                      {uploadResult.error_details.map((error, index) => (
                        <li key={index}>{error}</li>
                      ))}
                    </ul>
                  </>
                )}
              </Alert>
            )}

            <div className="text-end">
              <Button
                variant="secondary"
                className="me-2"
                onClick={() => setShowUploadModal(false)}
              >
                
              </Button>
              <Button variant="primary" type="submit">
                
              </Button>
            </div>
          </Form>
        </Modal.Body>
      </Modal>

      {/*    */}
      <Modal show={showEditModal} onHide={() => setShowEditModal(false)}>
        <Modal.Header closeButton>
          <Modal.Title> </Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <Form>
            <Form.Group className="mb-3">
              <Form.Label></Form.Label>
              <Form.Control
                type="text"
                value={editWord.english}
                onChange={(e) =>
                  setEditWord({ ...editWord, english: e.target.value })
                }
              />
            </Form.Group>

            <Form.Group className="mb-3">
              <Form.Label></Form.Label>
              <Form.Control
                type="text"
                value={editWord.korean}
                onChange={(e) =>
                  setEditWord({ ...editWord, korean: e.target.value })
                }
              />
            </Form.Group>

            <Form.Group className="mb-3">
              <Form.Label></Form.Label>
              <Form.Select
                value={editWord.level}
                onChange={(e) =>
                  setEditWord({ ...editWord, level: parseInt(e.target.value) })
                }
              >
                <option value={1}> 1</option>
                <option value={2}> 2</option>
                <option value={3}> 3</option>
                <option value={4}> 4</option>
                <option value={5}> 5</option>
              </Form.Select>
            </Form.Group>
          </Form>
        </Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={() => setShowEditModal(false)}>
            
          </Button>
          <Button variant="primary" onClick={handleSaveEdit}>
            
          </Button>
        </Modal.Footer>
      </Modal>
    </Container>
  );
};

export default AdminPage;
